.header {
    width: 1441px;
    height: 130px;
    margin: 0 auto;
    padding-top: 40px;
    padding-left: 80px;
    align-items: center;
    text-align: center;
}

.header2 {
    display: flex;
}

.header3 {
    display: flex;
}

.header ul {
    display: flex;
    list-style: none;
    margin-right: 361px;
}

.header ul li {
    margin-right: 42px;
    color: #697ca6;
    text-shadow: 0px 0px 5px #e6e6e6;
    cursor: pointer;
}

.header ul li:hover {
    color: #697ca6;
    cursor: pointer;
}

.circle {
    width: 26px;
    height: 26px;
    background-color: #5e81fe;
    border-radius: 50%;
    margin-right: 73px;
}

.imgGroup {
    display: flex;
}

.imgGroup img {
    margin-right: 37px;
    margin-top: 10px;
    cursor: pointer;
}

.button {
    display: flex;
    margin-left: 7px;
    width: 113px;
    height: 43px;
    background: #D2E1FF;
    border-radius: 2px;
    font-weight: bold;
    font-size: 13px;
    line-height: 16px;
    align-items: center;
    text-align: center;
    justify-content: center;
    color: #3A79FF;
    cursor: pointer;
}

.end {
    width: 470px;
    padding-top: 20px;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    justify-content: center;
    align-items: center;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: capitalize;
    color: #AEB8D0;
}

